/* src/Login.css */
.login-container {
  width: 300px;
  margin: 0 auto;
  padding: 20px;
  background-color:black;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.login-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.login-container form div {
  margin-bottom: 15px;
}

.login-container form label {
  display: block;
  font-weight: bold;
}

.login-container form input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.login-container form button {
  width: 100%;
  padding: 10px;
  background-color: #61dafb;
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
}

.login-container form button:hover {
  background-color: #21a1f1;
}
